/*
*
* ==========================================
* GENERAL
* ==========================================
*
*/

::-webkit-scrollbar {
    width: 0.3em;
    height: 0.3em;
}

::-webkit-scrollbar-thumb {
    background: $primary;
}

::-webkit-scrollbar-track {
    background: lighten($body-bg, 3%);
}

.border-md {
    border-width: 3px !important;
}

.text-uppercase {
    letter-spacing: 0 !important;
}

strong {
    font-weight: $font-weight-bold;
}

body {
    scrollbar-face-color: $brand-primary;
    scrollbar-track-color: #fff;
    overflow-x: hidden;
    &::-webkit-scrollbar {
        width: 5px;
        height: 5px;
    }
}

section {
    overflow: hidden;
    padding: 3rem;

    @include media-breakpoint-down(md) {
        padding: 3rem 1.5rem;
    }
}

h1 {
    @include media-breakpoint-down(md) {
        font-size: 2rem !important;
    }
}

body {
    position: relative;
    &::before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background: url(https://italianolight.netlify.com/img/pattern.a55eb102.png);
        background-size: 10rem;
        opacity: 0.04;
        z-index: -1;
    }
}

.container-fluid {
    max-width: 1400px;
}

.text-small {
    font-size: 0.9rem !important;
}

.sticky-top {
    top: 2rem;
}

.page-content {
    width: calc(100% - 14rem);
    margin-left: 14rem;
}

.sidebar {
    &-item:first-of-type {
        .sidebar-link {
            border-top: 1px solid lighten($body-bg, 5%);
        }
    }
    &-link {
        padding: 0.7rem 1.5rem;
        // font-family: $headings-font-family;
        color: $gray-200;
        background: darken($dark, 1%);
        display: block;
        width: 100%;
        margin-bottom: 0.25rem;
        font-size: 0.9rem;
        font-weight: 400;
        // transition: all 0.15s;
        position: relative;
        // border-left: 4px solid transparent;

        i {
            margin-right: 0.3rem;
            // transition: all 0.2s;
        }

        &.active {
            background: linear-gradient(to right, $brand-primary, adjust-hue($brand-primary, 10%)) !important;
            color: #fff !important;
            box-shadow: 0 3px 4px rgba($brand-primary, 0.44);
            border-color: transparent;
            margin-bottom: 0.3rem;
            border-radius: 0.25rem;

            &::before {
                opacity: 1;
                opacity: 0;
            }

            i {
                opacity: 1;
                margin-left: 0;
                margin-right: 0.3rem;
                // color: $brand-primary;
            }
        }

        &:focus,
        &:hover {
            color: $gray-100;
            background: darken($dark, 3%);
            text-decoration: none;
        }
    }
}

.index-forward {
    position: relative;
    z-index: 999;
}

.intro-list {
    max-width: 400px;

    .font-weight-bold {
        width: 150px;
        display: inline-block;
        font-family: $headings-font-family;
    }
}

.reset-anchor {
    color: inherit;
    transition: all 0.3s;

    &:hover,
    &:focus {
        text-decoration: none;
    }
}

.bg-heading-text,
.position-relative {
    position: relative;
}

.hero {
    span.bg-heading-text-content {
        font-size: 4rem;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
        opacity: 0.03;
    }
}

ol.custom-numbers {
    list-style: none;
}

ol.custom-numbers li {
    counter-increment: my-awesome-counter;
}

ol.custom-numbers li::before {
    content: counter(my-awesome-counter) ". \00a0 \00a0 ";
    color: $brand-primary;
    font-family: $headings-font-family;
    font-weight: 400;
    font-size: 0.85em;
    width: 2rem !important;
}

pre {
    font-size: 0.8rem;
    font-weight: 300;
    box-shadow: inset 0 0 25px rgba(12, 13, 22, 0.3);
    overflow: hidden;
    padding-top: 4rem !important;
    position: relative;
    margin-bottom: -0px;
    overflow-x: scroll;

    @include media-breakpoint-down(md) {
        font-size: 0.7rem;
    }

    // ol {
    //     min-width: 900px;
    // }
}

a {
    &.underlined {
        position: relative;

        &::after {
            content: "";
            width: 100%;
            height: 1px;
            border-bottom: 1px dashed #ccc;
            position: absolute;
            bottom: -1px;
            left: 0;
            display: block;
        }
    }
}

.pre-outer {
    overflow: hidden;
    border-radius: 1rem;
    background: lighten($body-bg, 3%);
    &::before {
        content: "";
        display: block;
        width: 100%;
        height: 2.5rem;
        background: lighten($body-bg, 2%);
        position: absolute;
        top: 0;
        left: 0;
        z-index: 999;
    }

    .pre-mockup {
        width: 0.7rem;
        height: 0.7rem;
        border-radius: 50%;
        background: #ec4b4b;
        position: absolute;
        top: 0.85rem;
        right: 1.5rem;
        z-index: 999;

        &::before,
        &::after {
            content: "";
            display: block;
            width: 0.7rem;
            height: 0.7rem;
            border-radius: 50%;
            background: #f6c444;
            position: absolute;
            top: 0;
            right: 1.4rem;
            z-index: 999;
        }

        &::after {
            background: #48da7a;
            right: 2.8rem;
        }
    }

    .pre-tab-indicator {
        height: 2.5rem;
        background: lighten($body-bg, 1%);
        line-height: 2.5rem;
        padding: 0 1.5rem;
        position: absolute;
        top: 0;
        left: 0;
        color: #e6e9ed !important;
        z-index: 999;
        border-radius: 0 30rem 30rem 0;
        font-size: 0.9rem;
        cursor: default;
        user-select: none;

        i {
            margin-right: 0.7rem;
            color: #e6e9ed !important;
        }
    }
}

.prettyprint {
    background: lighten($body-bg, 3%) !important;
}

ol.linenums {
    li {
        background: lighten($body-bg, 4%) !important;
    }
}

.demo-link {
    box-shadow: 0 0 7px rgba(0, 0, 0, 0.15);
    transition: all 0.2s;
    padding-bottom: 90%;
    background-size: 100% !important;
    background-position: top center !important;
    position: relative;
    overflow: hidden;
    // border-radius: 1rem;

    &-overlay {
        position: absolute;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.4);
        width: 100%;
        height: 100%;
    }

    &:hover {
        // transform: translateY(-5px);
        background-size: 104% !important;
    }
}

.text-shadow {
    text-shadow: 0 0 2px rgba(0, 0, 0, 0.4) !important;
}

.feature-item {
    transition: all 0.3s;
    background: $body-bg;
    box-shadow: 0 2px 7px rgba(0, 0, 0, 0.09);
    overflow: hidden;
    transform: translateY(0);
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        display: block;
        height: 4px;
        width: 100%;
        transition: all 0.3s;
        opacity: 1;
    }
    &::after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: url(../img/pattern.png);
        background-size: 12rem;
        opacity: 0;
        display: block;
        transition: all 0.3s;
        opacity: 0.04;
    }

    &:hover {
        transform: translateY(-4px);

        &::before {
            background: $brand-primary;
        }
    }
}

.btn {
    &:focus {
        box-shadow: none !important;
    }
}

.base-font-family {
    font-family: "Arsenal", sans-serif !important;
}

.lead {
    line-height: 1.9 !important;
}
.text-sm {
    font-size: 0.9rem !important;
}

/* ===============================================================
     SCROLL TOP BUTTON
  =============================================================== */
#scrollTop {
    background: $brand-primary;
    color: #fff;
    cursor: pointer;
    border-radius: 0.25rem 0 0 0.25rem;
    width: 100px;
    height: 40px;
    line-height: 40px;
    position: fixed;
    top: calc(50% - 20px);
    right: -110px;
    z-index: 9998;
    font-size: 0.8em;
    transition: all 0.3s;
    padding-right: 1rem;
    box-shadow: 0 0 5px rgba($brand-primary, 0.1);
}

#scrollTop.active {
    right: -60px;
}

#scrollTop:hover {
    right: 0;
}

#scrollTop i {
    margin: 0 20px;
}

.hero-bg {
    position: relative;

    &::after {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: #fff;
        opacity: 0.94;
    }
}

.demo-heading {
    @include media-breakpoint-up(lg) {
        font-size: 3.5rem !important;
    }
}
