.browser-wrapper {
    display: block;
    max-width: 1170px;
    width: 96%;
    // margin: 3vh auto;
    // height: 94vh;
    // max-height: 500px;
    background: #fff;
    border-radius: 7px;
    overflow: hidden;
    box-shadow: 9px 9px 30px 0 rgba(1, 50, 101, 0.21);
    box-shadow: 0 9px 15px rgba(0, 0, 0, 0.1);
}

.browser-head {
    display: flex;
    background: #fff;
    color: #1c4360;
    width: 100%;
    padding: 5px 1rem;
    align-items: center;
    height: 55px;
}

.window-controls {
    display: flex;
    width: 110px;
    justify-content: center;
    margin-right: 20px;

    span {
        display: block;
        width: 14px;
        height: 14px;
        background: #ededed;
        border-radius: 50%;
        margin-left: 5px;

        &:first-of-type {
            margin-left: 0;
        }
    }
}

.url-bar {
    display: flex;
    width: 100%;
    padding: 8px;
    border-radius: 6px;
    height: 40px;
    background: #f8f9fa;
    align-items: center;
    position: relative;
    border-right: 10px solid #f8f9fa;
}

.url-controls {
    display: flex;

    &__item {
        display: block;
        width: 25px;
        pointer-events: none;

        &.left {
            display: block;
        }

        &.right {
            display: block;
        }
    }
}

.url-protocol {
    padding: 10px;
    background: #ededed;
    border-radius: 5px;
    margin: 20px;
    color: #515151;
    opacity: 0.5;
    font-weight: 500;
    height: 30px;
    line-height: 30px;
    padding: 0 1rem;
    font-size: 0.9rem;
}

.url-txt {
    display: block;
    color: #515151;
    opacity: 0.5;
    font-weight: 500;

    &:hover {
        opacity: .8;
    }
}

.url-next {
    display: block;
    width: 25px;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    pointer-events: none;
}

.browser-inner {
    width: 100%;
    height: 100%;
    overflow: auto;
}

.browser-inner {
    max-height: 450px;
    overflow-y: scroll;
}

.scrollbar-track {
    background: transparent!important;
}

.scrollbar-thumb {
    background: rgba(50, 50, 50, 0.25)!important;
}
